



























import service from '../../service.js';

export default {
    data() {
        return {
            account: '',
            password: '',
        }
    },
    methods: {
        register() {
            /**
             * 客户端对账号信息进行一些必要的校验。
             * 实际开发中，根据业务需要进行处理，这里仅做示例。
             */
            if (this.account.length < 5) {
                uni.showToast({
                    icon: 'none',
                    title: '账号最短为 5 个字符'
                });
                return;
            }
            if (this.password.length < 6) {
                uni.showToast({
                    icon: 'none',
                    title: '密码最短为 6 个字符'
                });
                return;
            }

            const data = {
                account: this.account,
                password: this.password,
            }
            service.addUser(data);
            uni.showToast({
                title: '注册成功'
            });
            uni.navigateBack({
                delta: 1
            });
        }
    }
}



